<template>
    <Select
        class="noerror inline"
        :placeholder="t('pages.settings.base.vibration.title')"
        :optionList="optionList"
        :value="settings.vibration"
        :clearable="false"
        :useI18n="true"
        @change="onVibrationChange"
    />
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import stores from '@/stores';
    import Select from 'ðŸ”—/components/ui/Select.vue';

    const settings = stores.settings();
    const { t } = useI18n();

    const onVibrationChange = (payload) => {
        settings.setVibration(payload.value);
    }

    const optionList = [
        {
            icon: 'vibro',
            code: true,
            name: 'pages.settings.base.vibration.enabled'
        }, {
            icon: 'vibro-off',
            code: false,
            name: 'pages.settings.base.vibration.disabled'
        }
    ]
</script>

