<template>
    <div class="profile">
        <div class="avatar animate-1"
            ref="avatar"
            @click="animateAvatarAgainPlease"
        >
            <Img v-if="user?.profile?.profileImage?.resized"
                :preview="user?.profile?.profileImage?.resized"
                :original="user?.profile?.profileImage?.original"
                :alt="user?.profile?.lastName"
            />
        </div>

        <label class="title">{{ user?.profile?.firstName }}</label>
        <label class="roles" v-if="roles">{{ roles }}</label>

        <FileUploader 
            class="upload"
            icon="upload-img"
            :acceptedFormats="['image/png', 'image/jpeg', 'image/jpg', 'image/gif', 'image/jfif']"
            :loading="data.loading"
            @change="updateAvatar"
        />
    </div>
</template>

<script setup>
    import { computed, reactive, ref } from 'vue';
    import { useI18n } from 'vue-i18n';
    import stores from '@/stores';

    import Img from 'ðŸ”—/components/ui/Img.vue';
    import FileUploader from 'ðŸ”—/components/ui/FileUploader.vue';

    const avatar = ref(null);

    const user = stores.user();
    const modals = stores.modals();
    const { t } = useI18n();

    const data = reactive({
        loading: false
    });

    const roles = computed(() => {
        return user.profile?.roles?.filter(item => item.roleId !== 'user' && item.roleId !== 'admin').map(item => t(`constants.roles.${ item.roleId }`)).join(', ');
    });

    const updateAvatar = async (file) => {
        data.loading = true;

        await user.updateAvatar(file);

        data.loading = false;
        animateAvatarAgainPlease();
    };

    // =)
    const animateAvatarAgainPlease = () => {
        avatar?.value?.classList.remove('animate-1');
        setTimeout(() => {
            avatar?.value?.classList.add('animate-1');
        }, 10);

        if (!user.profile?.profileImage?.original) {
            returnl
        }

        modals.open('gallery', { data: { imgs: [{
            title: user.profile.firstName || user.profile.userName,
            ...user.profile?.profileImage
        }] } });
    }
</script>

<style lang="scss" scoped>
    div.profile {
        display: flex;
        flex-direction: column;
        align-items: center;
        gap: 8px;

        & > div.avatar {
            width: 120px;
            height: 120px;
            border-radius: 50%;
            margin-bottom: 12px;
            border: solid 3px var(--light-20);

            &.animate-1 {
                @include animage-booble-shadow;
            }

            & > img {
                width: 100%;
                height: 100%;
                border-radius: 50%;
                z-index: 1;
                position: relative;
            }
        }


        .title {
            font: var(--font-h6);
            font-weight: bold;
            color: var(--dark-500);
        }

        .roles {
            font: var(--font-caption);
            color: var(--dark-80);
            max-width: 264px;
            text-align: center;
        }

        .upload {
            position: absolute;
            bottom: 12px;
            right: 16px;
            transform: translateY(50%);
            z-index: 1;
        }
    }
</style>
