<template>
    <div class="head">
        <label class="title">{{ t('pages.settings.title') }}</label>    
    </div>

    <div class="block">
        <Profile v-if="user.profile?.id" />
    </div>

    <div class="block">
        <label class="title">{{ t('pages.settings.base.title') }}</label>

        <ul>
            <li class="clickable">
                <Language />
            </li>
            <li class="clickable">
                <Vibration />
            </li>
            <li class="clickable offset" @click="editFirstName">
                <Icon icon="user" />
                <span>{{ t('pages.settings.base.profile.title') }}</span>
            </li>
        </ul>
    </div>

    <div class="block">
        <label class="title">{{ t('pages.settings.sound.title') }}</label>

        <ul>
            <li class="clickable offset">
                <Toggler
                    class="reverse"
                    :inline="true"
                    :name="t('pages.settings.sound.autofade')"
                    :checked="true"
                />
            </li>
        </ul>
    </div>

    <div class="block">
        <label class="title">{{ t('pages.settings.theme.title') }}</label>

        <ul>
            <li class="offset">
                <Theme />
            </li>
        </ul>
    </div>

    <div class="block">
        <label class="title">{{ t('pages.settings.devices.title') }}</label>

        <ul>
            <li class="clickable offset" @click="authInStudio">
                <Icon icon="apps" />
                <span>{{ t('pages.settings.devices.studio') }}</span>
            </li>
        </ul>
    </div>
    <div class="block">
        <label class="title">{{ t('pages.settings.help.title') }}</label>

        <ul>
            <li class="clickable offset" @click="invite">
                <Icon icon="share" />
                <span>{{ t('pages.settings.help.invite') }}</span>
            </li>

            

            <li class="clickable offset" @click="mailUs">
                <Icon icon="email" />
                <span>{{ t('pages.settings.help.email') }}</span>
            </li>

            <li class="clickable offset" @click="openTelegramGroup">
                <Icon icon="telegram" />
                <span>{{ t('pages.settings.help.tg') }}</span>
            </li>

            <li class="clickable offset" @click="openTermsOfUse">
                <Icon icon="paperclip" />
                <span>{{ t('pages.settings.help.terms-of-use') }}</span>
            </li>
            <li class="clickable offset" @click="openDocumentation">
                <Icon icon="paperclip" />
                <span>{{ t('pages.settings.help.documentation') }}</span>
            </li>
        </ul>
    </div>
    
    <Ready page="settings" />
</template>

<script setup>
    import { computed, watch, defineAsyncComponent, onBeforeUnmount, onMounted } from 'vue';
    import { useI18n } from 'vue-i18n';
    import Toggler from 'ðŸ”—/components/ui/Toggler.vue';
    import Ready from 'ðŸ”—/components/ui/Ready.vue';
    import Theme from './components/Theme.vue';
    import Profile from './components/Profile.vue';
    import Language from './components/Language.vue';
    import Vibration from './components/Vibration.vue';
    // import { numberWithSpaces } from 'ðŸ”—/utils/utils';

    const { t } = useI18n();

    import stores from '@/stores';

    const modals = stores.modals();
    const user = stores.user();
    const nav = stores.nav();
    const tg = stores.tg();
    const others = stores.others();
    const share = stores.share();


    const authInStudio = () => {
        modals.open('studio-auth', {
            quietClose: true
        });
    };

    const editFirstName = () => {
        modals.open('edit-firstname', {
            quietClose: true
        });
    };

    const invite = () => {
        share.invite();
    };


    const mailUs = () => {
        tg.openLink({
            external_url: 'mailto:help@bilbomusic.com',
            target: '_blank'
        });
    }

    const openTelegramGroup = () => {
        tg.openLink({
            external_url: 'https://t.me/bilbomusic',
            type: 'telegram'
        });
    }

    const openTermsOfUse = () => {
        tg.openLink({
            'external_url': 'https://github.com/Bilbo-Music/bilbo-docs-public/blob/main/terms%20of%20service/Bilbo%20Music.md'
        });
    }

    const openDocumentation = () => {
        tg.openLink({
            'external_url': 'https://github.com/Bilbo-Music/bilbo-docs-public'
        });
    }

    modals.register({
        'studio-auth': defineAsyncComponent(() => import('./modals/ModalStudioAuth.vue')),
        'edit-firstname': defineAsyncComponent(() => import('./modals/ModalEditFirstName.vue'))
    });

    onMounted(() => {
        tg.showBackButton();
    });

    onBeforeUnmount(() => {
        tg.hideBackButton();
        modals.unRegister(['studio-auth', 'edit-firstname']);
    });
</script>

<style lang="scss" scoped>
    .head {
        @include page-title;
    }

    .title {
        font: var(--font-h6);
        font-weight: bold;
        color: var(--dark-500);
        padding: 0 16px;
        width: 100%;
        margin-bottom: 16px;
    }

    .block {
        display: flex;
        flex-direction: column;
        gap: 0;
        padding: 16px 0 32px 0;
        // background: var(--light-10);
        box-shadow: 0px 5px 6px -3px var(--dark-20);
        position: relative;
        width: 100%;

        & > ul {
            @include settings-list;
        }
    }
</style>
