<template>
    <Select
        class="noerror inline"
        :placeholder="t('pages.settings.base.language')"
        :optionList="locale.languages"
        :value="locale.locale"
        :clearable="false"
        @change="onLanguageChange"
    />
</template>

<script setup>
    import { useI18n } from 'vue-i18n';
    import stores from '@/stores';
    import Select from 'ðŸ”—/components/ui/Select.vue';

    const locale = stores.locale();
    const { t } = useI18n();

    const onLanguageChange = (payload) => {
        locale.updateLocale(payload.value);
    }
</script>

